	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Democrats Abroad Global Presidential Primary","date":"14 Jan 2008","slug":"/2008-01-14-democrats-abroad-global-presidential-primary/","tags":"Y2008"},"html":"<p>This is a public service announcement for expats of the Democratic persuasion.</p>\n<p>This year for the first time, <a href=\"http://www.democratsabroad.org/\">Democrats Abroad</a>, the overseas branch of the US Democratic Party, is offering the opportunity for expats to vote in a <a href=\"http://www.democratsabroad.org/article/2008/01/11/delegate-selection-plan-summary\">Global Presidential Primary</a> from February 5 to 12 to elect 22 delegates to the Democratic National Convention. You can vote over the internet, by fax, by mail or in person. You do need to be a US citizen eligible to vote in the general election in November, and you can vote either in the Global Primary or your home state's primary, but not both.</p>\n<p>To vote in the Global Primary, you need to join Democrats Abroad by January 31, which you can do at <a href=\"http://www.VoteFromAbroad.org\">http://www.VoteFromAbroad.org</a> or at <a href=\"http://www.democratsabroad.org/user/register\">Democrats Abroad</a>.</p>\n<p>For equal time, there is also an organization for <a href=\"http://www.republicansabroad.org/\">Republicans Abroad</a>. However, they are not part of the Republican National Committee, so they don't elect any delegates to the Republican National Convention. But they can certainly help you get set up to participate in the nomination process in your home state.</p>\n<p>But if you are a Republican, in all fairness, I hope that our guy (or girl) kicks your guy's butt in November.</p>"},"next":{"fields":{"title":"Electoral Compass","date":"13 Jan 2008","slug":"/2008-01-13-electoral-compass/"}},"previous":{"fields":{"title":"Proud of the Working Poor","date":"17 Jan 2008","slug":"/2008-01-17-proud-of-the-working-poor/"}}},{"node":{"fields":{"title":"Electoral Compass","date":"13 Jan 2008","slug":"/2008-01-13-electoral-compass/","tags":"Y2008"},"html":"<p><a href=\"http://www.electoralcompass.com/\"></p>\n<div style=\"text-align:center;\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2008/01/position.jpg\" alt=\"position.jpg\" border=\"0\" width=\"351\" height=\"235\" /></div>\n<p></a></p>\n<p>At <a href=\"http://www.electoralcompass.com/\">Electoral Compass USA</a> you can take a survey and compare yourself to the major US presidential candidates. But I already knew I liked Obama, so I didn't learn anything new. (<a href=\"http://www.davosnewbies.com/2008/01/10/the-us-electoral-compass/\">via Davos Newbies</a>)</p>"},"next":{"fields":{"title":"Coupons Coupons Coupons","date":"08 Jan 2008","slug":"/2008-01-08-coupons-coupons-coupons/"}},"previous":{"fields":{"title":"Democrats Abroad Global Presidential Primary","date":"14 Jan 2008","slug":"/2008-01-14-democrats-abroad-global-presidential-primary/"}}},{"node":{"fields":{"title":"Coupons Coupons Coupons","date":"08 Jan 2008","slug":"/2008-01-08-coupons-coupons-coupons/","tags":"Y2008"},"html":"<p>We didn't to be bored after our busy month in December, so we're running a coupon promotion for the next 3 weeks. These coupons are officially only valid for our restaurant, but many McDonald's in Germany are running similar promotions so you might be able to use them elsewhere.</p>\n<p><a href=\"http://www.mcdonalds-nordheide.de/wordpress/wp-content/uploads/2008/01/coupons-jan08.pdf\" title=\"coupons-jan08.pdf\"><img src=\"http://www.mcdonalds-nordheide.de/wordpress/wp-content/uploads/2008/01/winterfest-rueckseite.jpg\" alt=\"winterfest-rueckseite.jpg\" /></a></p>"},"next":{"fields":{"title":"McDonald's Takes On Starbucks","date":"07 Jan 2008","slug":"/2008-01-07-mcdonalds-takes-on-starbucks/"}},"previous":{"fields":{"title":"Electoral Compass","date":"13 Jan 2008","slug":"/2008-01-13-electoral-compass/"}}}],"pathPrefix":"","first":false,"last":false,"index":88,"pageCount":1101}}

